body {
  margin: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 198px;
  height: 406px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  display: flex;
}

.card__img {
  width: 160px;
  height: -moz-fit-content;
  height: fit-content;
  margin: 16px 0 36px;
}

.card__title {
  color: #060b35;
  margin: 4px 0;
  padding-inline: 16px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 17px;
  padding-inline: 16px;
  font-size: 10px;
}

.card__reviews {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  height: 16px;
  margin: 0 0 7px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  padding: 0 0 1px;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  display: flex;
}

.card__price-text {
  color: #616070;
}

.card__price-amount {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  padding: 11px 68.5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
  transition: color .5s, background-color .5s, border-color .5s;
  display: block;
}

.card:hover .card__button {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(n):not(:last-child) {
  margin-right: 4px;
}

.stars__star:nth-child(n):not(:nth-child(n+5)) {
  background-image: url("star-active.edda4c43.svg");
}

